import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import List from '@/components/Word'
import Lista from '@/components/List1'
import C from '@/components/Content'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
      {
          path: '/list',
          name: 'list',
          component: List
      },
      {
          path: '/list/1',
          name: 'list1',
          component: Lista
      },
      {
          path: '/content',
          name: 'content',
          component: C
      }
  ]
})



