<template>
    <div class="hello">
        <h1>{{msg}}</h1>
        <div class="number">{{number}}</div>
        <button @click="but">过</button>
        <button @click="but1">我爱你</button>
        <p>您点击了{{num}}次</p>
        <a href="#/list">zouni</a>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
        return {
            msg: 'Welcome to Your 7 游戏',
            number: this.get(0, 45),
            a: false,
            num: 0,
            arr: [7, 14, 17, 21, 27, 28, 35, 37, 42, 47, 49, 56, 57, 63, 67, 70, 71, 72, 73, 74, 75, 76, 77, 78, 79, 84, 87, 91, 97, 98]
        }
    },
    methods: {
//      这里是方法。
        but: function () {
            this.num++
            if (this.arr.indexOf(this.number) < 0) {
                this.number++
            } else {
                alert('结束了')
                this.number = this.get(0, 45)
                this.num = 0
            }
        },
        but1: function () {
            this.num++
            var s = this.arr.indexOf(this.number)
            if (s >= 0) {
                this.number++
            } else {
                alert('结束了')
                this.number = this.get(0, 45)
                this.num = 0
            }
        },
        get: function (m, n) {
            return Math.floor(Math.random() * (n - m + 1)) + m
        }
    },
    computed: {
//      这里是开始时候就执行。随机数
    }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .number{font-size: 46px;}
    button{padding: 10px 20px;margin-top: 20px;margin-right: 30px;font-size: 20px;outline: none;border-radius: 5px;}
</style>



